 } from \"@vue/reactivity\";\nimport { defineAsyncComponent } from \"vue\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { IHeaderButton } from \"../../../../domain/model/ui/IHeaderButton\";\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\nimport { INotification } from \"../../../../domain/presentation/INotification\";\nimport { IStudio } from \"../../../../domain/useCase/IStudio\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\nimport \"bootstrap/js/dist/dropdown\";\nimport { IApplication, IExportViewItem } from \"../../../../domain/model/models\";\nimport Tabs from \"../../../vue3/components/ui/tabs.vue\";\nimport { ITab, ITabs } from \"../../../vue3/components/ui/ITabs\";\nimport { ref } from \"vue\";\nimport { IGetExportSelectionData } from \"../../../../domain/useCase/IGetExportSelectionData\";\nimport { IRouter } from \"../../../../domain/presentation/IRouter\";\nimport ExportApp from \"../../components/application/exportApp.vue\";\nimport { ILocalization } from \"../../../../domain/infrastructure/ILocalization\";\nimport { ExportType, IExportJob } from \"@stechquick/algae/lib/qCloudTemp/exporter\";\nimport { ILogout } from \"../../../../domain/useCase/ILogout\";\nimport { TooltipImpl } from \"../../impls/tooltipImpl\";\nimport { ITimer } from \"../../../../domain/infrastructure/ITimer\";\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst studio = container.resolve<IStudio>(IStudio);\nconst notification = container.resolve<INotification>(INotification);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst selectionData = ref([] as unknown as Array<IExportViewItem>);\nconst router = container.resolve<IRouter>(IRouter);\nconst localization = container.resolve<ILocalization>(ILocalization);\nconst timer = container.resolve<ITimer>(ITimer);\nconst routerParams = router.getRouteParams<{ appID: string }>();\nconst checkedOut = computed(() => viewModel.studio.currentItem?.checkedOut);\nconst props = defineProps<{ app: IApplication }>();\nconst deployTip = ref(\"\");\nconst deployTooltipVisbile = ref(false);\nconst deployContentOpacity = ref(1);\nconst hoverValue = ref(false);\nconst tooltip = container.resolve<TooltipImpl>(TooltipImpl);\nlet deployBtnTimeout: NodeJS.Timeout | undefined;\nconst tablist = computed(() => {\n  return (\n    viewModel.studio?.openItems?.map((item) => {\n      const retVal: ITab = {\n        name: item.name,\n        type: item.modelType,\n        id: item.ID,\n        selected: viewModel.studio.currentItem?.ID == item.ID,\n        modified: item.modified?.model,\n        synced: item.synced,\n        cb: {\n          close: executor.wrap(async () => studio.closeItem(item), { loading: false }),\n          select: executor.wrap(async () => studio.openItem(item), { loading: true }),\n        },\n      };\n      return ref(retVal).value;\n    }) || []\n  );\n});\n\nfunction onCheckout() {\n  const currentItem = viewModel.studio.currentItem;\n  if (!currentItem) {\n    return;\n  }\n\n  executor.execute(async () => studio.checkout(currentItem), { loading: true });\n}\nfunction onSave() {\n  const currentItem = viewModel.studio.currentItem;\n  if (!currentItem) {\n    return;\n  }\n\n  executor.execute(async () => studio.save(currentItem), { loading: true });\n}\nfunction onSavePlus() {\n  dialog.showDialog(\n    defineAsyncComponent(() => import(\"../dialogs/studio/savePlus.vue\")),\n    { closable: true }\n  );\n}\nfunction onItemSelect(selectedItem: IHeaderButton) {\n  selectedItem.onSelect();\n}\n\nasync function release() {\n  executor.execute(async () => studio.release(viewModel.studio.currentItem!), { loading: false });\n}\n\nfunction listJobs(jobType: IExportJob[\"type\"]) {\n  const title = jobType == \"export\" ? \"Export Job List\" : \"Deploy Job List\";\n  dialog.showDialog(\n    defineAsyncComponent(() => import(`../export/exportJobList.vue`)),\n    { closable: true, title, width: \"1000px\", maxHeight: \"500px\", overflow: \"auto\" },\n    { jobType }\n  );\n}\n\nasync function openExportDialog(exportType: ExportType) {\n  executor.execute(\n    async () => {\n      const applicationID = routerParams.appID;\n      selectionData.value = await executor.executeUseCase<IGetExportSelectionData>(IGetExportSelectionData, { applicationID, exportType });\n      dialog.showDialog(\n        ExportApp,\n        {\n          closable: true,\n          title: \"Export: \" + viewModel.studio.appName,\n          image: { src: props.app?.logo || \"../../../../images/is-bankasi-logo.jpg\", width: \"50px\", height: \"50px\" },\n          width: \"1000px\",\n          maxHeight: \"700px\",\n          height: \"635px\",\n          overflow: \"hidden\",\n        },\n        { selectionData: selectionData.value, exportType }\n      );\n    },\n    { loading: true }\n  );\n}\nfunction goMainPage() {\n  router.push(\"/main/applicationList\");\n}\n\nfunction checkDeployConditions() {\n  return !viewModel.apps.find((app) => app.ID === viewModel.studio.appId)?.deployParameters?.gitProductRepoId;\n}\n\nfunction logout() {\n  executor.execute(\n    async () => {\n      await executor.executeUseCase<ILogout>(ILogout);\n      router.push(\"/login\");\n    },\n    { loading: true }\n  );\n}\n\nfunction showDeployTip() {\n  if (checkDeployConditions()) {\n    deployTip.value = localization.get(\"deployTip\");\n    return deployTip.value;\n  } else {\n    deployTip.value = \"\";\n    return deployTip.value;\n  }\n}\n\nfunction openDeployScreen(screenType: \"Export\" | \"Deploy\") {\n  dialog.showDialog(\n    defineAsyncComponent(() => import(`../dialogs/deploy/deployScreen.vue`)),\n    { closable: true, title: screenType, width: \"1417px\", height: \"741px\" },\n    { screenType }\n  );\n}\n\nfunction hoverLogo() {\n  hoverValue.value = true;\n}\nfunction unHover() {\n  hoverValue.value = false;\n}\nfunction deployBtnTooltipOver() {\n  deployTooltipVisbile.value = true;\n  deployContentOpacity.value = 1;\n  timer.reset();\n}\nfunction deployTooltipMouseLeave() {\n  deployTooltipVisbile.value = false;\n}\nfunction deployBtnTooltipMouseLeave() {\n  if (timer.isTimerRunning) {\n    return;\n  }\n  timer.setUp({\n    time: 3000,\n    callback: () => {\n      deployTooltipVisbile.value = false;\n      deployContentOpacity.value = 1;\n      timer.reset();\n    },\n    interval: {\n      time: 1000,\n      callback: () => {\n        deployContentOpacity.value = deployContentOpacity.value - 0.3;\n      },\n    },\n  });\n}\nfunction deployContentpOver() {\n  deployContentOpacity.value = 1;\n  timer.reset();\n}\nfunction openSettings() {\n  deployTooltipVisbile.value = false;\n  dialog.showDialog(\n    defineAsyncComponent(() => import(`../dialogs/deploySettings.vue`)),\n    { closable: true, title: \"Deploy Settings\", width: \"1100px\", height: \"497px\" }\n  );\n}\n</script>\n<template>\n  <div class=\"top-menu\">\n    <div class=\"row top-container\">\n      <div class=\"col-lg-1 home-section\" style=\"display: flex\">\n        <div class=\"corner-container\">\n          <div\n            class=\"logo-container\"\n            @click=\"goMainPage\"\n            @mouseover=\"hoverLogo()\"\n            @mouseleave=\"unHover()\"\n            :style=\"{ 'background-color': hoverValue ? '#151233' : '', padding: '18px' }\"\n          >\n            <img v-if=\"!hoverValue\" src=\"../../../../images/ide5-logo.svg\" alt=\"ide5-logo\" width=\"24\" height=\"24\" />\n            <img v-if=\"hoverValue\" src=\"../../../../images/plateau_hover.svg\" alt=\"ide5-logo\" />\n          </div>\n        </div>\n        <div class=\"vl\"></div>\n      </div>\n\n      <div class=\"col-lg-8 middle-resize\">\n        <div class=\"middle-container\">\n          <div class=\"title-container\">\n            <img src=\"../