    );
  }
  treeviewFilterData.value = {};
  pagesFilterData.value = {};
  entitiesFilterData.value = {};
  processesFilterData.value = {};

  itemsCache.value.forEach((item) => {
    switch (item.objectType) {
      case "module":
        pagesFilterData.value[item.ID] = [];
        entitiesFilterData.value[item.ID] = [];
        processesFilterData.value[item.ID] = [];
        (item as IModule).children.forEach((children) => {
          if (children.objectType === "model") {
            if (children.modelType === "qjson" || (children as IModel).modelType == "js" || (children as IModel).modelType === "yaml") {
              pagesFilterData.value[item.ID].push(children);
            } else if (children.modelType === "entityDesigner") {
              entitiesFilterData.value[item.ID].push(children);
            } else if (children.modelType === "process" || children.modelType === "bpmn") {
              processesFilterData.value[item.ID].push(children);
