function createPopupOpen() {
  if (createPopupVisible.value == false) {
    createPopupVisible.value = true;
  } else {
    createPopupVisible.value = false;
  }
}

function toggleFilter(type: "ui" | "entity" | "process") {
  switch (type) {
    case "ui":
      viewModel.studio.filters.ui = showPages.value = !showPages.value;
      break;
    case "entity":
      viewModel.studio.filters.entity = showEntities.value = !showEntities.value;
      break;
    case "process":
      viewModel.studio.filters.process = showProcesses.value = !showProcesses.value;
      break;
    default:
      break;
  }
  if (cacheItems) {
    itemsCache.value = viewModel.studio.items;
    cacheItems = false;
  }
  filterTreeview();
  showModels();
}

function showModels() {
  filteredModels.value = [];
  if (showPages.value) filteredModels.value.push("qjson");
  if (showEntities.value) filteredModels.value.push("entityDesigner");
  if (showProcesses.value) {
    filteredModels.value.push("process");
    filteredModels.value.push("bpmn");
  }

  sidebarManager.reset();
  viewModel.studio.items = itemsCache.value
    .map((item) => ({
      ...item,
      children: (showPages.value && item.objectType == "model" ? treeviewFilterData.value[item.ID] || [] : [])
        .concat(showPages.value ? pagesFilterData.value[item.ID] || [] : [])
        .concat(showEntities.value ? entitiesFilterData.value[item.ID] || [] : [])
        .concat(showProcesses.value ? processesFilterData.value[item.ID] || [] : []),
    }))
    .filter((item) => item.objectType == "module" || showPages.value);

  sidebarManager.show(item.sidebarComponent);
}

function searchItem() {
